.main-section {
  margin: 32px;
  width: 70%;
  border: 5px #0e4f1f solid;
}
.main-section:first-child {
  background: #bfffd0;
}
.main-section:nth-child(2) {
  background: #e9ffef;
}
.main-section:nth-child(3) {
  background: #2ddf5c;
}
.main-section a {
  padding: 36px;
  text-decoration: none;
  display: inline-block;
}
.main-section__title {
  color: black;
}
